aside.eltdf-sidebar {
    position: relative;
    z-index: 11;
    margin: 0 0 60px;

    .widget {
        color: $default-text-color;

        &:last-child {
            margin: 0 !important;
        }
    }
}

.wpb_widgetised_column {
    position: relative;

}

aside.eltdf-sidebar,
.wpb_widgetised_column {
    // widgets spacing
    $widget-bottom-margin: 10px;
    $widget-bottom-padding: 15px;
    $widget-inner-padding: 15px;

    // widget color
    $widget-background-color: #eeeeee;

    // list items dimensions
    $widget-list-item-height: 20px;
    $widget-list-item-padding: 2px;
    $widget-gallery-padding: 0;
    $widget-list-product-item-padding: 5px;

    // list items color
    $widget-list-item-background-color: #ffffff;

    // list items border
    $widget-list-item-border-color: $default-border-color;
    $widget-list-item-border-width: 1px;
    $widget-list-item-border-style: solid;

    // text
    $widget-text-color: inherit;
    $widget-heading-color: #000000;

    // links
    $widget-link-color: $default-text-color;
    $widget-link-hover-color: darken($default-text-color, 35%);

    // specific widgets
    $widget-search-form-height: $input-height;
    $widget-tag-cloud-height: 30px;
    $widget-select-margin-top: 13px;
    $widget-search-title-margin-bottom: 28px;
    $widget-search-margin-bottom: 51px;
    $widget-additional-color: $default-text-color;
    
    @import 'widgets/_default-wordpress-widgets-flat';
    @import 'widgets/standalone-widgets';
}