/* ==========================================================================
   Default WordPress widgets styles - begin
   ========================================================================== */

.widget {
    margin: 0 0 $widget-bottom-margin;
    padding: 0;

    .eltdf-widget-title-holder {
        margin-bottom: $widget-bottom-padding;

        .eltdf-widget-title {
            color: $widget-heading-color;
            margin-bottom: 3px;
        }

        .eltdf-separator {
            border-bottom-width: 3px;
            border-color: $widget-text-color;
            width: 30px;
        }
    }

    ul {
        list-style: none;
        margin: 0;
        padding: 0;

        li {
            display: block;
            line-height: $widget-list-item-height;
            margin: 0;
            padding: $widget-list-item-padding 0;

            &:last-child {
                border: 0;
                padding-bottom: 0;
            }

            a {
                color: $widget-link-color;
                line-height: 27px;
                letter-spacing: 0;

                &:hover {
                    color: $first-main-color;
                }
            }
        }

        .children,
        .sub-menu {
            border-top: 1px solid $widget-list-item-border-color;
            margin-top: $widget-list-item-padding;
            padding-left: 5px;
        }
    
        .children{
            border: none;
            padding: 0 0 0 20px;
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and animation on link hover

    &.widget_pages,
    &.widget_archive,
    &.widget_categories,
    &.widget_meta,
    &.widget_recent_entries,
    &.widget_nav_menu {
        .eltdf-widget-title {
            margin-bottom: 0;
        }

        ul {
            li {
                overflow: hidden;

                a {
                    display: inline-block;
                    vertical-align: middle;
                    overflow: hidden;
                    padding: 0 15px 0 0;
                    position: relative;

                    @include eltdfTransition(color .2s ease);
                }
            }
        }
    }
    
    &.widget_search{
        margin-bottom: $widget-search-margin-bottom;
        .eltdf-widget-title-holder{
            margin-bottom: $widget-search-title-margin-bottom;
        }
    }
    
	&.widget_archive,
	&.widget_categories {
		ul li a {
			padding-right: 0;
		}
	}
    
    &.eltdf-blog-list-widget{
        .eltdf-widget-title{
            margin-bottom: 18px;
        }
    }
    
    &.eltdf-author-info-widget{
        .eltdf-aiw-title{
            margin: 0 0 29px;
        }
	    .eltdf-aiw-text{
		    margin: 18px 0 0;
	    }
    }
	
    &.widget_products{
		.eltdf-widget-title{
			margin: 0 0 24px;
		}
        .product_list_widget{
            li{
                padding: $widget-list-product-item-padding 0;
            }
        }
	}
	
        
        ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // widgets with lists and w/o animation on link hover

    &.widget_recent_comments,
    &.widget_rss {
        .eltdf-widget-title {
            margin-bottom: 0;
        }
    }
    
    &.widget_recent_comments{
        #recentcomments{
            .recentcomments{
                color: $widget-additional-color;
            }
        }
    }
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // calendar

    #wp-calendar {
        text-align: center;
        width: 100%;

        caption {
            margin: 0 0 $widget-list-item-padding;
            color: $widget-additional-color;
        }
        
        thead{
            color: $widget-additional-color;
        }
        
        tbody{
            color: $widget-additional-color;
        }

        th, td {
            padding: 3px 4px;
        }

        td#today {
            background-color: $widget-list-item-background-color;
            color: $widget-text-color;
        }
    
        td#prev {
           text-align: left;
        }
    
        td#next {
            text-align: right;
        }
        
        tfoot a {
            color: $widget-link-color;

            &:hover {
                color: $widget-link-hover-color;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // rss

    &.widget_rss {
    
        .eltdf-widget-title{
            margin-bottom: 21px!important;
        }
        
        ul{
            li{
                border-bottom: 1px solid $first-main-color;
                padding: 20px 0;
            
                &:first-child{
                    padding-top: 0;
                }
            }
        }
    
        .rss-date, cite {
            display: block;
            margin-bottom: 5px;
            text-transform: uppercase;
            font-size: 12px;
            font-weight: 600;
        }
    
        .rssSummary{
            font-size: 14px;
            font-weight: 400;
            line-height: 22px;
            color: $widget-additional-color;
        }

        .rss-widget-icon {
            display: inline-block;
        }

        .rss-date {
            display: block;
            margin-bottom: $widget-list-item-padding;
            
        }

        cite {
            display: block;
            margin-top: calc(#{$widget-list-item-padding} / 2);
            
        }
    
        .rsswidget{
            font-size: 19px;
            font-weight: 600;
            line-height: 25px;
            margin-bottom: 5px;
            color: $widget-additional-color;
        
            &:hover{
                text-decoration: underline;
                text-underline-position: under;
            }
        }
        .eltdf-widget-title{
            .rsswidget{
                font-size: 29px;
            }
        }
    }
    
    
    &.eltdf-author-info-widget{
        .eltdf-aiw-inner{
            .eltdf-aiw-image{
                border-radius: 0;
            }
        }
        .eltdf-author-social-icons{
            a{
                font-size: 11px;
                font-weight: 500;
                letter-spacing: 0.12em;
                color: $first-main-color;
                line-height: 25px;
                text-transform: uppercase;
            }
        }
    }
    &.widget_media_image{
        margin: 0 0 7px;
    }
    &.eltdf-image-gallery-widget{
        -webkit-box-sizing: border-box;
        -moz-box-sizing: border-box;
        box-sizing: border-box;
        padding: 0 $widget-gallery-padding;
    }
    
    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // search

    .eltdf-searchform {
        .input-holder {
            border-color: #dac4ab;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            padding-left: 27px;
            padding-right: 9px;

            input,
            button {
                background-color: transparent;
                border: 0;
                border-radius: 0;
                box-sizing: border-box;
                color: $widget-text-color;
                display: inline-block;
                float: left;
                font-family: $default-text-font;
                font-size: 15px;
                font-weight: inherit;
                height: $widget-search-form-height;
                outline: 0;
                position: relative;
                vertical-align: top;
                -webkit-appearance: none;

                @include eltdfTransition(color .2s ease-in-out);
            }

            button {
                line-height: calc(#{$widget-search-form-height} - 2px);
            }

            input.search-field {
                width: calc(100% - #{$widget-search-form-height});

                @include eltdfPlaceholder {
                    color: $widget-text-color;
                }
            }

            button {
                //background: $widget-list-item-background-color;
                color: $widget-text-color;
                cursor: pointer;
                font-size: 13px;
                width: $widget-search-form-height;

                @include eltdfTransition(color .2s ease-in-out);

                &:hover {
                    color: $widget-link-hover-color;
                }

                span {
                    display: block;
                    left: 0;
                    line-height: inherit;
                    position: absolute;
                    top: 1px;
                    width: 100%;

                    &:before {
                        display: block;
                        line-height: inherit;
                    }
                }
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // combobox

    //&.widget_archive, // to affect dropdown in woo widgets
    //&.widget_categories,
    //&.widget_text {
        select {
            background-color: transparent;
            border-color: $widget-list-item-border-color;
            border-radius: 0;
            border-style: $widget-list-item-border-style;
            border-width: $widget-list-item-border-width;
            box-sizing: border-box;
            color: $widget-text-color;
            display: inline-block;
            font-family: $default-text-font;
            font-size: 15px;
            font-weight: inherit;
            height: $widget-search-form-height;
            line-height: calc(#{$widget-search-form-height} - 2px);
            margin-top: $widget-select-margin-top;
            outline: 0;
            padding: 0 0 0 21px;
            position: relative;
            vertical-align: top;
            width: 100%;

            @include eltdfTransition(color .2s ease-in-out);

            option {
            }
        }
    ul{
        li{
            color: $widget-additional-color;
        }
    }
    //}

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // tag cloud

    .tagcloud {
        margin: 24px 0 65px;
        a {
            display: inline-block;
            font-family: $default-text-font;
            font-weight: inherit;
            line-height: $widget-tag-cloud-height;
            margin-top: 4px;
            font-size: 15px!important;
            background-color: $default-button-color;
            color: #fff;
            padding: 2px 18px 3px;

            &:hover {
                background-color: #d9ad7c;
            }
        }
    }

    ////////////////////////////////////////////////////////////////////////////////////////////////////////////////////
    // text

    &.widget_text {
        img {
            display: block;
        }

        p {
            &:first-child {
                margin-top: 0;
                padding-top: $widget-list-item-padding;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }
    }
}