@import 'variables';
@import 'mixins';

/* ==========================================================================
   #Comments styles - start
   ========================================================================== */
.etienne-newsletter {

    .eltdf-form-title {
        color: #fff;
        margin: 10px 0;
    }

    .eltdf-form-holder {
        position: relative;

        .eltdf-form-right {
            position: absolute;
            top: 0;
            right: -20px;
        }

        button.wpcf7-form-control {
            background-color: transparent !important;
            color: #cca06f !important;
            padding: 17px 28px !important;

            span.eltdf-btn-text {
                font-family: "linea-arrows-10" !important;
                font-style: normal !important;
                font-weight: normal !important;
                font-variant: normal !important;
                text-transform: none !important;
                speak: none;
                line-height: 1;
                -webkit-font-smoothing: antialiased;
                -moz-osx-font-smoothing: grayscale;
                font-size: 23px;
                vertical-align: middle;
            }

            &:hover {
                background-color: transparent !important;
                border-color: transparent !important;
            }
        }

        .wpcf7-form-control {
            &.wpcf7-text,
            &.wpcf7-number,
            &.wpcf7-date,
            &.wpcf7-textarea,
            &.wpcf7-select,
            &.wpcf7-quiz {
                @extend %input-style;
                color: #f1f1f1;
                border: 0;
                background-color: #181d31;
                font-size: 12px;
                padding: 23px 30px;

                &:focus {
                    color: #f1f1f1;
                    &::placeholder{
                        color: #f1f1f1;
                    }
                }
    
                &::placeholder{
                    color: $second-additional-color;
                }
            }
            
        }
    }
    span{
        .wpcf7-not-valid-tip{
            color: $second-additional-color;
            font-size: 12px;
        }
    }
}
.wpcf7-validation-errors{
    color: $second-additional-color;
    border-color: $second-additional-color!important;
    font-size: 12px;
}

.eltdf-comment-holder {
    @include eltdfRelativeHolderLayout();
    padding: 0;
    box-sizing: border-box;

    .eltdf-comment-holder-inner {
        padding: 66px 0 20px;
    }

    .eltdf-comment {
        &.eltdf-pingback-comment {

            .eltdf-comment-text {
                padding: 0;
            }
        }
    }

    .eltdf-comment-list {
        position: relative;
        padding: 0;
        margin: 0;
        list-style: none;
        z-index: 150;

        li {
            @include eltdfRelativeHolderLayout();
            margin: 25px 0 0;
            padding: 17px 0 0;
        }

        > li:first-child {
            margin: 0;
            padding: 0;
            border: 0;
        }

        .children {
            padding: 0 0 0 113px;
        }
    }

    .eltdf-comments-title {
        margin-bottom: 24px;

        > * {
            margin: 0;
        }
    }

    .eltdf-comment-image {
        position: relative;
        top: 10px;
        left: 0;
        width: 93px;
        height: 96px;
        display: block;
        float: left;

        img {
            display: block;
        }
    }

    .eltdf-comment-text {
        padding: 0 0 0 128px;
        min-height: 96px;

        .eltdf-comment-info {
            @include eltdfRelativeHolderLayout();
            margin: -7px 0 3px;
        }

        .replay,
        .comment-reply-link,
        .comment-edit-link {
            float: left;
            font-size: 12px;
            font-weight: 500;
            color: $first-main-color;
            text-transform: uppercase;
            text-decoration: underline;
            letter-spacing: 0.12em;
            
            &:hover {
                color: $default-heading-color;
            }
        }
	
	    .comment-edit-link {
		    margin-left: 10px;
	    }

        .eltdf-comment-name {
            float: left;
            margin: 0;
        }

        .eltdf-comment-date {
            float: left;
            margin: 0;
            font-size: 11px;
            font-weight: 500;
            letter-spacing: 0.12em;
            text-transform: uppercase;
            color: $first-main-color;
        }

        .eltdf-text-holder p {
            margin: 4px 0 14px;
        }

        .comment-respond {
            margin: 20px 0;

            .comment-reply-title {
                margin: 0 0 10px;
                font-size: 18px;
            }

            .logged-in-as {
                margin: 0 0 8px;
            }
        }

        #cancel-comment-reply-link {
            margin-left: 10px;
            font-size: 18px;
            color: $first-main-color;

            &:hover {
                color: $default-heading-color;
            }
        }

        ul, ol {

            li {
                display: list-item;
            }
        }

        li {
            margin: 0 !important;
            padding: 0 !important;
            border: 0 !important;
        }
    }
}

.eltdf-comment-form {
    padding: 0;
    clear: both;
    box-sizing: border-box;

    .eltdf-comment-form-inner {
        padding: 45px 0 78px;
    }

    .comment-reply-title {
        margin: 0 0 31px;
    }

    .logged-in-as {
        margin: 0 0 8px;
    }
    
    .comment-form-cookies-consent {
        margin-bottom: 36px;

        input[type="checkbox" i],
        input[type="checkbox"],
        input:matches([type="checkbox"]) {
            margin: 3px 3px 3px 4px;
        }
        
        label{
            top: -1px;
            position: relative;
        }
    }
}

@include ipad-portrait {

    .eltdf-comment-holder {

        .eltdf-comment-list {

            .children {
                padding: 0 0 0 40px;
            }
        }
    }
}

@include phone-landscape {

    .eltdf-comment-holder {

        .eltdf-comment-list {

            .children {
                padding: 0;
            }
        }

        .eltdf-comment-text {

            .eltdf-comment-date,
            .eltdf-comment-name {
                float: none;
            }

            .replay,
            .comment-reply-link,
            .comment-edit-link {
                float: none;
                margin: 0 10px 0 0;
            }
        }
    }
}

@include phone-portrait {

    .eltdf-comment-holder {

        .eltdf-comment-image {
            float: none;
        }

        .eltdf-comment-text {
            padding: 20px 0 0;
            min-height: inherit;
        }
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

#respond textarea,
.eltdf-style-form textarea,
#respond input[type='text'],
input[type='text'],
input[type='email'],
input[type='password'],
.post-password-form input[type='password'] {
    @extend %input-style;
    border-color: #dac4ab;
    font-size: 14px;
    font-weight: 300;
    color: #6a6a6a;
}

#respond textarea,
.eltdf-style-form textarea{
    cursor: text;
}

.post-password-form input[type='password'] {
    margin-top: 10px;
}

.wpcf7-form-control {
    &.wpcf7-text,
    &.wpcf7-number,
    &.wpcf7-date,
    &.wpcf7-textarea,
    &.wpcf7-select,
    &.wpcf7-quiz {
        @extend %input-style;

        footer & {
            margin: 0;
        }
    }
}

.qodef-newsletter{
    text-align: center;
    
    .your-name{
        margin-right: 22px;
        input{
            width: calc((100% - 26px)/2);
            cursor: text;
        }
    }
    
    .your-email{
        input{
            width: calc((100% - 26px)/2);
            cursor: text;
        }
    }
    
    .ajax-loader{
        display: none!important;
    }
	
	@include phone-landscape{
		.your-name,  .your-email{
			input{
				width: 100%;
			}
		}
	}
}

//#submit_comment, // added our button classes in comments.php
//input.wpcf7-form-control.wpcf7-submit, // added our button classes in contact-form-7-confing.php
.post-password-form input[type='submit']{
    @include eltdfButtonDefaultStyle();
    @include eltdfButtonSize();
    @include eltdfButtonSolidColor();

    cursor: pointer;

    &:hover {
        @include eltdfButtonSolidHoverColor();
    }
}

p.form-submit,
.wpcf7-form p {
    margin: 0;
}

#respond textarea,
.eltdf-style-form textarea,
.wpcf7-form textarea,
div.woocommerce textarea {
    display: block;
    overflow: auto;
}

.eltdf-two-columns-form-without-space {
    .wpcf7-form .ajax-loader {
        display: none;
    }
}

/* ==========================================================================
   #Comments styles - end
   ========================================================================== */

/* ==========================================================================
   #Global Owl Slider - begin
   ========================================================================== */
.eltdf-owl-slider {
    visibility: hidden;

    .owl-stage-outer {
        z-index: 1;
    }

    .owl-stage {
        transition-timing-function: $aux-easing !important;
    }

    &.eltdf-slider-is-moving {
        .owl-item {
            .eltdf-block-drag-link {
                z-index: -1 !important;
            }
        }
    }

    &.eltdf-slider-has-both-nav {
        .owl-nav {
            .owl-prev,
            .owl-next {
                $pos: calc(-50% - 21px); // 21 is is half height of pagination control + top margin
                @include eltdfTransform(translateY($pos));
            }
        }
    }

    &.eltdf-slider-hide-pagination .owl-dots {
        display: none;
    }

    .owl-nav {

        .owl-prev,
        .owl-next {
            position: absolute;
            top: 50%;
            padding: 0;
            margin: 0;
            color: $first-main-color;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;
            z-index: 3;

            @include eltdfTransform(translateY(-50%));
            @include eltdfTransition(color .2s ease-out);
            
            > span {
                position: relative;
                display: inline-block;
                vertical-align: middle;
                font-size: 46px;
                line-height: 1;
                @include eltdfTransitionTransform(.6s cubic-bezier(0.075, 0.82, 0.165, 1));

                &:before {
                    display: block;
                    line-height: inherit;
                }
            }
        }

        .owl-prev {
            left: 40px;

            &:hover {
                > span {
                    @include eltdfTransform(translateX(-6px));
                }
            }
        }

        .owl-next {
            right: 40px;

            &:hover {
                > span {
                    @include eltdfTransform(translateX(6px));
                }
            }
        }
    }

    .owl-dots {
        text-align: center;
        margin: 16px 0 0;

        .owl-dot {
            display: inline-block;
            vertical-align: middle;
            padding: 0;
            margin: 0 6px;
            background: none;
            border: 0;
            border-radius: 0;
            outline: none;
            -webkit-appearance: none;

            span {
                display: inline-block;
                vertical-align: middle;
                width: 12px;
                height: 12px;
                border-radius: 3em;
                background-color: transparent;
                border: 2px solid rgba($default-heading-color, .5);
                box-sizing: border-box;

                @include eltdfTransition(background-color .2s ease-out, border .2s ease-out);
            }

            &:hover,
            &.active {

                span {
                    background-color: $default-heading-color;
                    border-color: $default-heading-color;
                }
            }
        }
    }

    .owl-carousel .owl-item img {
        -webkit-transform: translateZ(0); //chrome text cut off fix
    }

    + .eltdf-slider-thumbnail {
        display: inline-block;
        vertical-align: middle;
        list-style: none;
        margin: 20px -10px 0;

        > .eltdf-slider-thumbnail-item {
            float: left;
            padding: 0 10px;
            margin-bottom: 20px;
            box-sizing: border-box;
            line-height: 0;
            cursor: pointer;

            img {
                border: 2px solid transparent;

                @include eltdfTransition(border-color 0.2s ease-out);
            }

            &.active {
                img {
                    border: 2px solid $first-main-color;
                }
            }
        }

        $thumbnail_columns_number: ('two', 'three', 'four', 'five', 'six');

        @for $i from 0 to length($thumbnail_columns_number) {
            &.eltdf-slider-columns-#{nth($thumbnail_columns_number,$i+1)} {

                .eltdf-slider-thumbnail-item {
                    width: 100% / ($i+2);

                    @include ipad-landscape {
                        width: 33.3333%;
                    }
                }
            }
        }
    }
}

/* ==========================================================================
   #Global Owl Slider - end
   ========================================================================== */

/* ==========================================================================
   #404 styles - start
   ========================================================================== */

.error404 {
    .eltdf-wrapper {
        padding: 0 !important;
    }

    .eltdf-content {
        height: 100vh;
    }

    &.admin-bar {
        .eltdf-content {
            height: calc(100vh - 32px);
        }
    }

    .eltdf-content-inner {
        @include eltdfTableLayout();
    }

    .eltdf-page-not-found {
        @include eltdfTableCellLayout();
        padding: 0 20%;
        text-align: center;
        bottom: 12px;

        .eltdf-404-title {
            margin: 0;
        }

        .eltdf-404-subtitle {
            margin: 35px 0 0;
        }

        .eltdf-404-text {
            margin: 10px 0 0;
        }

        .eltdf-btn {
            margin: 30px 0 0;

            &.eltdf-btn-light-style {
                color: #585858;
                background-color: #fff;
                border-color: #fff;

                &:hover {
                    color: #fff !important;
                    background-color: transparent !important;
                    border-color: #fff !important;
                }
            }
        }
    }

    @include ipad-landscape {
        .eltdf-content {
            height: 100vh;
        }

        &.admin-bar {
            .eltdf-content {
                height: 100vh;
            }
        }

        .eltdf-page-not-found {
            padding: 107px 23% 0;
        }
    }

    @include ipad-portrait {
        .eltdf-page-not-found {
            padding: 107px 30px 20px;
        }
    }
}

/* ==========================================================================
   #404 styles - end
   ========================================================================== */

/* ==========================================================================
   #Back to Top styles - start
   ========================================================================== */
#eltdf-back-to-top {
    position: fixed;
    width: 41px;
    right: 25px;
    bottom: 25px;
    z-index: 10000;
    opacity: 0;

    &.off {
        opacity: 0;
        @include eltdfTransition(.0s .6s);

        > span {
            &:after {
                opacity: 0;
                @include eltdfTransform(scale(0.4));
                @include eltdfTransition(.4s $default-easing .2s);
            }

            span {
                opacity: 0;
                @include eltdfTransition(opacity .3s);
            }
        }
    }

    &.on {
        opacity: 1;

        > span {
            &:after {
                opacity: 1;
                @include eltdfTransform(scale(1));
                @include eltdfTransition(.4s $default-easing);
            }

            span {
                opacity: 1;
                @include eltdfTransition(opacity .4s $default-easing .15s);
            }
        }
    }

    > span {
        position: relative;
        display: block;
        width: 41px;
        height: 41px;
        line-height: 40px;
        font-size: 20px;
        text-align: center;
        color: #fff;
        overflow: hidden;

        span {
            position: relative;
            z-index: 10;
            display: block;
            line-height: inherit;
            opacity: 0;
    
            &:before {
                display: block;
                margin-left: -1px;
            }
        }
        
        &:after {
            border-radius: 50%;
            content: '';
            @include eltdfAbsoluteHolderLayout;
            background-color: $first-main-color;
            @include eltdfTransform(scale(.4));
            opacity: 0;
        }

        &:hover {
            &:after {
                background-color: #d9ad7c;
            }
        }
    }
}

/* ==========================================================================
   #Back to Top styles - end
   ========================================================================== */

